<script>
	export default {
		name: 'Header',
		data() {

			return {
				show: false,
				scrollPosition: null,
				activeTab: '',
				activeSubTab: ''
			}
		},
		mounted() {
			window.addEventListener('scroll', this.updateScroll);
		},
		methods: {
			toggleColor() {
				var app = document.getElementsByTagName("html")[0];
				if (localStorage.lightMode == "dark") {
					localStorage.lightMode = "light";
					app.setAttribute("data-theme", "light");
				} else {
					localStorage.lightMode = "dark";
					app.setAttribute("data-theme", "dark");
				}
			},
			updateScroll() {
				this.scrollPosition = window.scrollY
			},
			toggleMenu() {
				this.show = !this.show

			},
			showMenu(value) {
				// console.log('called', value)
				if (value == this.activeTab) {
					this.activeTab = ''
				} else {
					this.activeTab = value
				}

			},
			showSubMenu(value) {
				// console.log('called', value)
				if (value == this.activeSubTab) {
					this.activeSubTab = ''
				} else {
					this.activeSubTab = value
				}

			}
		}
	}
</script>


<style lang="scss" scoped>
.toggle {
	width: 26px;
	height: 20px;
	position: relative;

	span {
		display: block;
		width: 100%;
		height: 2px;
		background: var(--font-stl-two);
		border-radius: 2px;

		&:after,
		&:before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			height: 2px;
			background: var(--font-stl-two);
			border-radius: 2px;
		}

		&:after {
			top: 0px;
		}

		&:before {
			bottom: 0px;
		}
	}

	&.toggleOpen {
		margin: 15px 10px;
	}

	&.toggleClose {
		margin: 12px 12px 12px auto;

		span {
			background: none;

			&:after {
				top: auto;
				transform: rotate(45deg);
				background: var(--font-stl-one);
			}

			&:before {
				bottom: auto;
				transform: rotate(-45deg);
				background: var(--font-stl-one);
			}
		}
	}
}

.btn-dark-light {
	svg {
		width: 25px;
		fill: #fff
	}
}

.logo {
	svg {
		height: auto;
		max-width: 150px;
		width: 100%;
		fill: #fff;
	}
}

.header {
	position: absolute;
	left: 0px;
	right: 0;
	top: 0px;
	z-index: 999;
	transition: all 0.3s ease-in-out;
	&.fixed{

	}
}

ul.menu {
	position: relative;

	li {
		position: relative;
	}

	li>a {
		font-size: 12px;
		font-weight: 700;
		text-transform: uppercase;
		display: block;
		padding: 10px 10px;
		letter-spacing: 1px;
	}

	.sub-menu>li>a {
		text-transform: capitalize;
	}
}

.fixed {
