			position: absolute;
			top: 100%;
			padding: 10px 0px;
			min-width: 180px;
			background: rgba(19, 20, 22, 0.9);
			box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.35);
			transition: all 0.3s;
			opacity: 0;
			visibility: hidden;

			li {
				padding: 0px 25px;
				display: block;

				a {
					position: relative;
					padding: 0px 0px;
					font-weight: 400;
					overflow: hidden;
					display: inline-block;

					&:after {
						content: " ";
						position: absolute;
						left: 0;
						bottom: 0;
						width: calc(0% - 1px);
						height: 2px;
						background-color: var(--s-color);
						transition: all 0.3s;
					}

					&:hover {
						color: #fff;

						&:after {
							width: calc(100% + 1px);
						}
					}
				}

				&:hover {
					&>a {
						color: #fff;

						&:after {
							width: calc(100% + 1px);
						}
					}
				}
			}
		}

		&>li {
			&>.sub-menu {
				transform: translateY(0px);

				li {
					.sub-menu {
						right: -100%;
						top: 50%;
						transform: translate(20px, -50%);
					}
				}
			}
		}

		&>li:hover {
			&>.sub-menu {
				transform: translateY(0px);
				opacity: 1;
				visibility: visible;
			}
		}

		&>li>.sub-menu li:hover>.sub-menu {
			transform: translate(0px, -50%);
			opacity: 1;
			visibility: visible;
		}
	}

	ul.menu {
		&>li>a {
			color: #fff;
		}

		.sub-menu {
			li {
				a {
					color: rgba(240, 240, 240, 0.7);
				}
			}
		}
	}

	.fixed {
